import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { removeAgent } from 'store/Agents/actions';
import { selectRowsSelected } from 'store/Agents/selectors';
import IconButton from 'Common/Components/IconButton';
import useModal from 'Hooks/useModal';
import Modal from 'Common/Components/Modal';
import { useIntl } from 'react-intl';
import isEmpty from 'lodash/isEmpty';
import { ReactComponent as WarningIcon } from 'Images/warning-delete.svg';
import { Wrapper, Trash } from './styled';

const Delete = () => {
  const dispatch = useDispatch();
  const intl = useIntl();
  const [ModalWarning, showModalWarning, toggleModalWarning] = useModal(Modal);
  const props = useSelector((state) => ({
    rowsSelected: selectRowsSelected(state)
  }));
  const acceptModal = () => {
    dispatch(removeAgent());
    toggleModalWarning(false);
  };
  return !isEmpty(props.rowsSelected) &&
    (
      <Wrapper>
        <IconButton title="Delete" onClick={ () => toggleModalWarning(true) } icon={ <Trash /> } />
        {showModalWarning && <ModalWarning icon={ <WarningIcon /> } title={ intl.formatMessage({ id: 'agents.delete.title' }) } onAccept={ acceptModal } onClose={ () => toggleModalWarning(false) }>{ intl.formatMessage({ id: 'agents.delete.message' }) }</ModalWarning>}
      </Wrapper>
    );
};
export default Delete;
