import fetchApi from 'services/api/connector';

export const fetchAgents = (ws) => fetchApi(`ws/${ws}/agents`, { method: 'GET' });
export const removeAgent = (ws, id) => fetchApi(`ws/${ws}/agents/${id}`, { method: 'DELETE' });
export const fetchToken = (data) => fetchApi('agent_token', {
  method: 'GET',
  data,
  headers: { 'Content-Type': 'multipart/form-data' }
});
export const fetchAgentTools = (ws, id) => fetchApi(`ws/${ws}/agents/${id}`);

export const runAgentTool = (ws, id, toolName, args, token) => fetchApi(`ws/${ws}/agents/${id}/run`, {
  method: 'POST',
  data: {
    csrf_token: token,
    executorData: {
      agent_id: id,
      executor: toolName,
      args
    }
  },
  headers: {
    'Content-type': 'application/json'
  }
});
