export function setAttachment (file) {
  return async (dispatch) => {
    const uploadImage = await toBase64(file);
    dispatch({
      type: SET_ATTACHMENT_MANAGE_CREATE_UPDATE,
      // value: uploadImage,
      name: file.name,
      content_type: file.type,
      data: uploadImage.split(';base64,')[1]
    });
  };
}

