import api from 'services/api';
import { selectCurrentWorkspace } from 'store/Faraday/selectors';

import {
  AGENTS_FETCHING, AGENTS_TABLE_GET_DATA, AGENTS_TABLE_SELECT_ROW, AGENTS_FAIL, AGENTS_GET_TOKEN, AGENTS_GET_TOOLS, AGENTS_CREATE_WIZARD_RESET, AGENTS_CREATE_WIZARD_ADD_TOOL, AGENTS_CREATE_WIZARD_REMOVE_TOOL, AGENTS_CREATE_WIZARD_SET_FIELDS_TOOL, AGENTS_SUCCESS, AGENTS_GET_MANIFEST, RESET_NEW_AGENT, SET_NEW_AGENT
} from './constants';

export function fetching () {
  return async (dispatch) => {
    dispatch({ type: AGENTS_FETCHING, isFetching: true });
  };
}

export function somethingWentWrong (errors) {
  return async (dispatch) => {
    dispatch({ type: AGENTS_FAIL, message: 'There was an error, please try again.', errors });
  };
}

export function selectAgentRow (rowSelectedInfo) {
  return async (dispatch) => {
    dispatch({ type: AGENTS_TABLE_SELECT_ROW, rowSelectedInfo });
  };
}

export function getAgents () {
  return async (dispatch, getState) => {
    try {
      dispatch(fetching());
      const workspace = selectCurrentWorkspace(getState());
      const response = await api.agents.fetchAgents(workspace);
      dispatch({ type: AGENTS_TABLE_GET_DATA, data: response });
    } catch (e) {
      dispatch(somethingWentWrong());
    }
  };
}

export function removeAgent () {
  return async (dispatch, getState) => {
    try {
      dispatch(fetching());
      const workspace = selectCurrentWorkspace(getState());
      const { rowsSelected } = getState().agents.table.selected;
      await Promise.all(rowsSelected.map(async (row) => await api.agents.removeAgent(workspace, row.id)));

      dispatch(getAgents());
    } catch (e) {
      dispatch(somethingWentWrong());
    }
  };
}

export function getToken () {
  return async (dispatch) => {
    try {
      dispatch(fetching());
      const responseSession = await api.faraday.getSession();

      const fd = new FormData();
      fd.set('csrf_token', responseSession.csrf_token);

      const responseToken = await api.agents.fetchToken(fd);
      dispatch({
        type: AGENTS_GET_TOKEN, token: responseToken.token, tokenExpiresIn: responseToken.expires_in, tokenDuration: responseToken.total_duration
      });
    } catch (e) {
      dispatch(somethingWentWrong(e));
    }
  };
}

export function getAgentTools (id) {
  return async (dispatch, getState) => {
    try {
      dispatch(fetching());
      const workspace = selectCurrentWorkspace(getState());
      const response = await api.agents.fetchAgentTools(workspace, id);
      dispatch({ type: AGENTS_GET_TOOLS, id, tools: response.executors });
    } catch (e) {
      dispatch(somethingWentWrong());
    }
  };
}

export function runAgentTool (agentId, toolName, args) {
  return async (dispatch, getState) => {
    try {
      dispatch(fetching());
      const workspace = selectCurrentWorkspace(getState());
      const response = await api.faraday.getSession();
      await api.agents.runAgentTool(workspace, agentId, toolName, args, response.csrf_token);

      dispatch(getAgents());
      dispatch({ type: AGENTS_SUCCESS });
    } catch (e) {
      dispatch(somethingWentWrong(e));
    }
  };
}

export function resetWizard () {
  return async (dispatch) => {
    dispatch({ type: AGENTS_CREATE_WIZARD_RESET });
  };
}

export function addTool (toolName) {
  return async (dispatch) => {
    dispatch({ type: AGENTS_CREATE_WIZARD_ADD_TOOL, tool: { name: toolName, variables: [] } });
  };
}

export function removeTool (tool) {
  return async (dispatch) => {
    dispatch({ type: AGENTS_CREATE_WIZARD_REMOVE_TOOL, tool });
  };
}

export function setFieldsTool (toolName, fields) {
  return async (dispatch) => {
    dispatch({ type: AGENTS_CREATE_WIZARD_SET_FIELDS_TOOL, toolName, fields });
  };
}

export function getManifest (ws) {
  return async (dispatch) => {
    try {
      dispatch(fetching());
      const data = await api.manage.fetchManifest(ws);
      dispatch({ type: AGENTS_GET_MANIFEST, data });
    } catch (e) {
      dispatch(somethingWentWrong(e));
    }
  };
}

export const setAgent = (data) => (dispatch) => {
  dispatch({ type: SET_NEW_AGENT, data });
};

export function clearAgent () {
  return (dispatch) => {
    dispatch({ type: RESET_NEW_AGENT });
  };
}
