import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { getAgents, getAgentTools, selectAgentRow } from 'store/Agents/actions';
import {
  selectFields, selectIsFetching, selectData, selectRowsSelected, selectLastRowSelected, selectTotal, selectLastIndexForRange, selectLastSelected
} from 'store/Agents/selectors';
import { selectCurrentWorkspace } from 'store/Faraday/selectors';
import Table from 'Common/Components/Table';
import getColumns from 'Screens/Automation/components/Agents/components/Columns';
import { openModal } from 'store/modals/actions';
import { MODAL_RUN_AGENT } from 'store/modals/modals';
import { Wrapper, TableWrapper } from './styled';
import ActionBar from './components/ActionBar';
import TriggerAgent from './components/TriggerAgent';
import EmptyAgents from './components/EmptyAgents';

const TableContainer = () => {
  const dispatch = useDispatch();
  const workspaceSelected = useSelector(selectCurrentWorkspace);

  const props = useSelector((state) => ({
    fields: selectFields(state),
    isFetching: selectIsFetching(state),
    data: selectData(state),
    total: selectTotal(state),
    rowsSelected: selectRowsSelected(state),
    lastRowSelected: selectLastRowSelected(state),
    lastIndexForRange: selectLastIndexForRange(state),
    lastSelected: selectLastSelected(state)
  }));

  const noAgents = props.data.length === 0 && !props.isFetching;

  useEffect(() => {
    dispatch(getAgents());
  }, [dispatch, workspaceSelected]);

  const onTriggerAgent = (e, id) => {
    dispatch(getAgentTools(id));

    dispatch(openModal(MODAL_RUN_AGENT));
    e.stopPropagation();
  };

  return (
    <TableWrapper>
      { noAgents
        ? <EmptyAgents />
        : (
          <Table
            selectRowAction={ selectAgentRow }
            isFetching={ props.isFetching }
            data={ props.data }
            rowsSelected={ props.rowsSelected }
            lastRowSelected={ props.lastRowSelected }
            lastIndexForRange={ props.lastIndexForRange }
            lastSelected={ props.lastSelected }
            columns={ getColumns(props.fields, onTriggerAgent, props.isFetching) }
          />
          )}
    </TableWrapper>
  );
};

const Agents = () => (
  <Wrapper>
    <TriggerAgent />
    <ActionBar />
    <TableContainer />
  </Wrapper>
);

export default Agents;
