import React from 'react';
import { Accept, Cancel } from '../Button/styled';
import {
  Wrapper, WrapperTitle, Title, Footer, Message
} from './styled';

const Modal = ({
  title, icon, children, onAccept, onClose
}) => (
  <Wrapper>
    { (icon || title) &&
      (
      <WrapperTitle>
        {icon}
        <Title>{title}</Title>
      </WrapperTitle>
      )}
    <Message>{ children }</Message>
    { (onClose || onAccept) &&
      (
      <Footer>
        { onClose ? <Cancel onClick={ onClose }>Close</Cancel> : null }
        { onAccept ? <Accept onClick={ onAccept }>Accept</Accept> : null }
      </Footer>
      )}
  </Wrapper>
);

export default Modal;
