import StandardDropdown from 'Common/Components/StandarDropdown';
import isEmpty from 'lodash/isEmpty';
import React from 'react';
import { Help, Here } from './styled';
import Content from './Content';

const ToolParams = ({
  currentTool, toolParams, onChangeField, onChangeTool
}) => !isEmpty(toolParams) && (
<>
  <StandardDropdown width="200px" field="toolParams" options={ !isEmpty(toolParams) && toolParams.map((t) => ({ name: t.name, desc: t.name })) } updateValue={ onChangeTool } />
  {!isEmpty(currentTool) && !isEmpty(currentTool.parameters_metadata) && <Content parameters={ currentTool.parameters_metadata } onChangeField={ onChangeField } />}
  { currentTool.website && (
  <Help>
    If you need further help, you can check this tool documentation
    <Here target="_blank" href={ currentTool.website }>here</Here>
  </Help>
  )}
</>
);
export default ToolParams;
