import React, { useState, useEffect } from 'react';
import isEmpty from 'lodash/isEmpty';
import get from 'lodash/get';
import { useIntl } from 'react-intl';
import { useDispatch, useSelector } from 'react-redux';
import { runAgentTool } from 'store/Agents/actions';
import selectModal from 'store/modals/selectors';
import {
  selectAgentTools, selectTriggerAgentId, selectIsFetching, selectAgentError, selectAgentSuccess
} from 'store/Agents/selectors';
import { Cancel } from 'Common/Components/Button/styled';
import BlueButton from 'Common/Components/BlueButton';
import ToolParams from 'Screens/Automation/components/Shared/ToolParams';
import { closeModal } from 'store/modals/actions';
import { MODAL_RUN_AGENT } from 'store/modals/modals';
import {
  Wrapper, Title, Subtitle, WrapperButtons, ModalWrapper
} from './styled';

const TriggerAgent = () => {
  const dispatch = useDispatch();
  const intl = useIntl();
  const props = useSelector((state) => ({
    agentId: selectTriggerAgentId(state),
    tools: selectAgentTools(state),
    isFetching: selectIsFetching(state),
    error: selectAgentError(state),
    success: selectAgentSuccess(state),
    showModal: selectModal(state, MODAL_RUN_AGENT)
  }));

  const [currentTool, setCurrentTool] = useState({});
  const [params, setParams] = useState({});

  useEffect(() => {
    setCurrentTool({});
    setParams({});
  }, [props.showModal]);

  useEffect(() => {
    if (props.success) {
      dispatch(closeModal(MODAL_RUN_AGENT));
    }
  }, [dispatch, props.success]);

  const onChangeTool = (field, value) => {
    const tool = props.tools.find((t) => t.name === value);
    setParams({});
    setCurrentTool(tool);
  };

  const onChangeField = (field, value) => {
    const newParams = { ...params };
    newParams[field] = value;
    if (value === '') {
      delete newParams[field];
    }
    setParams(newParams);
  };

  const disableAccept = () => {
    if (isEmpty(currentTool)) return true;

    const response = Object.keys(currentTool.parameters_metadata).filter((toolName) => {
      const required = currentTool.parameters_metadata[toolName].mandatory;
      return required && isEmpty(get(params, toolName, ''));
    });
    return !isEmpty(response);
  };

  const run = async () => {
    dispatch(runAgentTool(props.agentId, currentTool.name, params));
  };
  const onClose = () => dispatch(closeModal(MODAL_RUN_AGENT));

  return (!isEmpty(props.tools) && props.showModal)
    ? (
    <ModalWrapper>
      <Wrapper>
        <Title>{intl.formatMessage({ id: 'agents.triggerAgent.title' })}</Title>
        <Subtitle>{intl.formatMessage({ id: 'agents.triggerAgent.subtitle' })}</Subtitle>
        <ToolParams toolParams={ props.tools } currentTool={ currentTool } onChangeTool={ onChangeTool } onChangeField={ onChangeField } />
        <WrapperButtons>
          <Cancel onClick={ onClose }>{intl.formatMessage({ id: 'agents.triggerAgent.cancel' })}</Cancel>
          <BlueButton onClick={ run } disabled={ disableAccept() } isLoading={ props.isFetching } label={ intl.formatMessage({ id: 'agents.triggerAgent.run' }) } />
        </WrapperButtons>
      </Wrapper>
    </ModalWrapper>

      )
    : null;
};
export default TriggerAgent;
