/* eslint-disable react/jsx-key */
import React from 'react';
import { useSelector } from 'react-redux';
import Checkbox from 'Common/Components/Checkbox';
import { selectToolError } from 'store/Agents/selectors';
import {
  Content as Wrapper, FieldWrapper, Label, Field, CheckboxWrapper
} from './styled';

const Content = ({ parameters, onChangeField }) => {
  const booleanParams = Object.keys(parameters).filter((param) => parameters[param].base === 'boolean');
  const nonBooleanParams = Object.keys(parameters).filter((param) => parameters[param].base !== 'boolean');

  const error = useSelector((state) => selectToolError(state));
  const capitalize = (str) => str.toString().charAt(0).toUpperCase() + str.toString().slice(1);
  const extractName = (str) => capitalize(str.replace('_', ' '));
  const type = (base) => {
    switch (base) {
      case 'boolean':
        return 'checkbox';
      case 'integer':
        return 'number';
      case 'string':
        return 'text';
      default:
        return 'text';
    }
  };

  return (
    <Wrapper>
      { nonBooleanParams.map((param) => <NonBooleanFields param={ param } parameters={ parameters } type={ type } extractName={ extractName } error={ error } onChangeField={ onChangeField } />) }
      {booleanParams.map((param) => <BooleanFields param={ param } parameters={ parameters } extractName={ extractName } onChangeField={ onChangeField } />)}
    </Wrapper>
  );
};

const NonBooleanFields = ({
  parameters, param, type, extractName, error, onChangeField
}) => {
  const paramType = type(parameters[param].base);
  const required = parameters[param].mandatory;
  const paramError = error(param);

  return (
    <FieldWrapper key={ param } required={ required }>
      <Label>{extractName(param)}</Label>
      <Field onChange={ (e) => onChangeField(param, e.target.value) } failed={ paramError } title={ paramError ? paramError.data[0] : param } type={ paramType } />
    </FieldWrapper>
  );
};

const BooleanFields = ({
  param, parameters, extractName, onChangeField
}) => {
  const required = parameters[param].mandatory;
  return (
    <CheckboxWrapper key={ param } required={ required }>
      <Label>{extractName(param)}</Label>
      <Checkbox onChange={ (e) => onChangeField(param, !e.currentTarget.checked) } />
    </CheckboxWrapper>
  );
};

export default Content;
