import React from 'react';
import { useDispatch } from 'react-redux';
import { Cancel } from 'Common/Components/Button/styled';
import { clearAgent } from 'store/Agents/actions';
import { Wrapper, SecondContainer, Next } from './styled';

const Header = ({
  back, cancel, next, finish, nextDisable
}) => {
  const dispatch = useDispatch();
  const onCancel = () => {
    dispatch(clearAgent());
    cancel();
  };
  return (
    <Wrapper>
      {back && <Cancel onClick={ back }> Back </Cancel>}
      <SecondContainer>
        {cancel && <Cancel onClick={ onCancel } id="header-auto">Cancel</Cancel>}
        {next && <Next label="Next" onClick={ next } disabled={ nextDisable } />}
        {finish && <Next onClick={ finish } label="Finish" />}
      </SecondContainer>
    </Wrapper>
  );
};

export default Header;
