import React, { useState } from 'react';
import filter from 'lodash/filter';
import head from 'lodash/head';
import some from 'lodash/some';
import { useSelector, useDispatch } from 'react-redux';
import { selectActiveWorkspaces } from 'store/Workspace/selectors';
import SearchList from 'Common/Components/SearchList';
import WorkspaceList from 'Common/Components/WorkspaceList';
import RadioButton from 'Common/Components/RadioButton/index';
import isValidUrl from 'Common/Helpers/Common';
import { setAgent } from 'store/Agents/actions';
import Header from '../Header';
import {
  Wrapper, Title, TextItem, TextInput, Input, WrapperField, Label, ContentWrapper
} from '../styled';
import DefaultServer from './styled';

const Configuration = ({ nextStep, onClose }) => {
  const dispatch = useDispatch();
  const agentData = useSelector((state) => state.agents.createNewAgent);
  const [agentName, setAgentName] = useState(agentData.name);
  const [agentWorkspaces, setAgentWorkspaces] = useState(agentData.workspaces);
  const [agentServer, setAgentServer] = useState(agentData.server);

  const [defaultChecked, setDefaultChecked] = useState(agentData.defaultChecked);
  const [customChecked, setCustomChecked] = useState(!agentData.defaultChecked);

  const props = useSelector((state) => ({
    allWorkspaces: selectActiveWorkspaces(state)
  }));
  const getWorkspaceList = () => props.allWorkspaces.map((ws) => ({ primary: ws.name, secondary: `${ws.stats.total_vulns}`, id: ws._id }));

  const onChangeAgentName = (e) => {
    setAgentName(e.target.value);
  };
  const onChangeAgentWorkspaces = (items) => {
    setAgentWorkspaces(items);
  };
  const onRemoveWs = (ws) => {
    // remove item from state
    const items = agentWorkspaces.filter((w) => w.id !== ws.id);
    onChangeAgentWorkspaces(items);
  };
  const onSelectWs = (ws) => {
    if (!some(agentWorkspaces, { id: ws.id })) {
      // add item to state
      const newItem = head(filter(props.allWorkspaces, { id: ws.id }));
      const items = [...agentWorkspaces, newItem];
      onChangeAgentWorkspaces(items);
    }
  };
  const check = (type) => {
    if (type === 'default') {
      setDefaultChecked(true);
      setCustomChecked(false);
    } else {
      setDefaultChecked(false);
      setCustomChecked(true);
    }
  };

  const onNext = () => {
    nextStep();
    const data = {
      name: agentName,
      server: defaultChecked ? window.location.origin : agentServer,
      workspaces: agentWorkspaces,
      defaultChecked
    };
    dispatch(setAgent(data));
  };

  const nextEnable = agentName.length > 0 && (defaultChecked || isValidUrl(agentServer)) && agentWorkspaces.length > 0;

  return (
    <Wrapper>
      <Header cancel={ onClose } next={ onNext } nextDisable={ !nextEnable } />
      <ContentWrapper>
        <Title>Configuration</Title>
        <TextItem>Generate a configuration file to be imported from the command line</TextItem>
        <TextInput>Name</TextInput>
        <Input placeholder="MyAgent" onChange={ onChangeAgentName } value={ agentName } />
        <TextInput>Server</TextInput>
        <TextItem>Select where to register your Agent</TextItem>
        <RadioButton label="Default" checked={ defaultChecked } onChange={ () => check('default') } />
        <DefaultServer>{window.location.origin}</DefaultServer>
        <RadioButton label="Custom" checked={ customChecked } onChange={ () => check('custom') } />
        <Input disabled={ defaultChecked } placeholder="https://myURL.com" onChange={ (e) => setAgentServer(e.target.value) } value={ agentServer } />
        <TextItem>URL’s and IP’s are valid</TextItem>
        <TextInput>Workspace(s)</TextInput>
        <WrapperField>
          <Label>Results will be included in this workspace</Label>
          <SearchList
            onSelect={ onSelectWs }
            items={ getWorkspaceList() }
          />
          { agentWorkspaces.length > 0 && <WorkspaceList workspaces={ agentWorkspaces } onRemove={ onRemoveWs } /> }
        </WrapperField>
      </ContentWrapper>
    </Wrapper>
  );
};

export default Configuration;
