import React from 'react';
import { useSelector } from 'react-redux';

import startCase from 'lodash/startCase';
import { selectAgentTypes, selectStepChooseTools, selectToolsSelected } from 'store/Agents/selectors';
import Url from 'url-parse';
import { Download, Text } from './styled';

const Dispatcher = () => {
  const { name, server, workspaces } = useSelector((state) => state.agents.createNewAgent);
  const props = useSelector((state) => ({
    agentTypes: selectAgentTypes(state),
    toolsSelected: selectToolsSelected(state),
    stepChooseTools: selectStepChooseTools(state),
    wsPort: state.faraday.wsPort
  }));

  const getParams = (items) => {
    let result = '';
    Object.keys(items).forEach((paramName) => {
      const values = items[paramName];
      result += `         ${paramName}:\n`;
      Object.keys(values).forEach((key) => {
        result += `           ${key.toLowerCase()}: ${values[key]}\n`;
      });
    });
    return result;
  };

  const getEnvVars = (name) => {
    const { variables } = props.stepChooseTools.find((i) => i.name === name);
    const variableList = Object.keys(variables);
    if (variableList.length === 0) return ' {}';
    return `\n${variableList.map((key) => `         ${key.toUpperCase()}:  ${variables[key]}`).join('\n')}`;
  };

  const getToolConf = (tool) => {
    if (!tool) return;
    return (
      `     ${`${tool.name}:\n` +
      '       max_size: 65536\n' +
      '       params:\n'}${
        getParams(tool.arguments)
      }` +
      `       repo_executor: ${tool.name}.py\n` +
      `       varenvs:${
        getEnvVars(tool.name)
      }\n`);
  };

  const getApiPort = (ssl, port) => {
    if (port) return port;
    if (ssl) return 443;
    return 5985;
  };

  const getWebSocketPort = (ssl, port) => {
    if (port) return props.wsPort;
    if (ssl) return 443;
    return 80;
  };

  const getFileAsString = (agentName, agentServer, agentWorkspaces) => {
    const newUrl = agentServer.indexOf('http') === -1 ? `http://${agentServer}` : agentServer;
    const url = new Url(newUrl);

    const host = url.hostname;
    const ssl = (url.protocol.indexOf('https') > -1);
    const port = url.port;
    const apiPort = getApiPort(ssl, port);
    const webSocketPort = getWebSocketPort(ssl, port);

    const fullText = '' +
      ' agent:\n' +
      `   agent_name: ${agentName}\n` +
      '   executors:\n' +
      `${props.toolsSelected.map((tool) => getToolConf(props.agentTypes[tool.name])).join('')}\n` +
      ' server:\n' +
      `   api_port: '${apiPort}'\n` +
      `   host: ${host}\n` +
      `   ssl: '${startCase(ssl)}'\n` +
      '   ssl_cert: \'\'\n' +
      '   ssl_ignore: \'False\'\n' +
      `   websocket_port: '${webSocketPort}'\n` +
      '   workspaces:\n' +
      `${agentWorkspaces.map((i) => `   - '${i.name}'\n`).join('')}\n`;

    return fullText;
  };

  const saveAgent = (agentName, agentServer, agentWorkspaces) => {
    const element = document.createElement('a');
    const fileTxt = getFileAsString(agentName, agentServer, agentWorkspaces);
    const file = new Blob([fileTxt], { type: 'text/plain' });
    element.href = URL.createObjectURL(file);
    element.download = 'dispatcher.yaml';
    document.body.appendChild(element);
    element.click();
  };
  return (
    <Text onClick={ () => saveAgent(name, server, workspaces) }>
      <Download />
      dispatcher.yaml
    </Text>
  );
};

export default Dispatcher;
