import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getVulnsCountBySeverity } from 'Screens/Dashboard/actions/Actions';
import { Col, Row } from 'Common/Components/Grid/index';
import {
  PieChart,
  Pie,
  Sector,
  Cell,
  ResponsiveContainer
} from 'recharts';
import { DASHBOARD_FILTERS } from 'store/Filters/constants';
import { setFilter } from 'store/Filters/actions';
import { selectCurrentWorkspace } from 'store/Faraday/selectors';
import colors from 'Styles/colors';
import { redirect } from 'store/Router/actions';
import { Empty, Title, Wrapper } from './styled';

const renderActiveShape = (props) => {
  const RADIAN = Math.PI / 180;
  const {
    cx, cy, midAngle, innerRadius, outerRadius, startAngle, endAngle,
    fill, payload, percent, value
  } = props;
  const sin = Math.sin(-RADIAN * midAngle);
  const cos = Math.cos(-RADIAN * midAngle);
  const sx = cx + (outerRadius + 10) * cos;
  const sy = cy + (outerRadius + 10) * sin;
  const mx = cx + (outerRadius + 30) * cos;
  const my = cy + (outerRadius + 30) * sin;
  const ex = mx + (cos >= 0 ? 1 : -1) * 22;
  const ey = my;
  const textAnchor = cos >= 0 ? 'start' : 'end';

  return (
    <g>
      <text x={ cx } y={ cy } dy={ 8 } textAnchor="middle" fill={ fill }>{payload.name}</text>
      <Sector
        cx={ cx }
        cy={ cy }
        innerRadius={ innerRadius }
        outerRadius={ outerRadius }
        startAngle={ startAngle }
        endAngle={ endAngle }
        fill={ fill }
      />
      <Sector
        cx={ cx }
        cy={ cy }
        startAngle={ startAngle }
        endAngle={ endAngle }
        innerRadius={ outerRadius + 6 }
        outerRadius={ outerRadius + 10 }
        fill={ fill }
      />
      <path d={ `M${sx},${sy}L${mx},${my}L${ex},${ey}` } stroke={ fill } fill="none" />
      <circle cx={ ex } cy={ ey } r={ 2 } fill={ fill } stroke="none" />
      <text x={ ex + (cos >= 0 ? 1 : -1) * 12 } y={ ey } textAnchor={ textAnchor } fill="#333">{value}</text>
      <text x={ ex + (cos >= 0 ? 1 : -1) * 12 } y={ ey } dy={ 18 } textAnchor={ textAnchor } fill="#999">
        {`${(percent * 100).toFixed(1)}%`}
      </text>
    </g>
  );
};

const SeverityChart = () => {
  const [activeIndex, setActiveIndex] = useState(0);
  const dispatch = useDispatch();
  const props = useSelector((state) => (
    {
      vulnCountBySeverity: state.dashboard.vulnCountBySeverity,
      workspaceSelected: selectCurrentWorkspace(state)
    }));

  const data = [];
  if (props.vulnCountBySeverity.critical > 0) data.push({ id: 'critical', name: 'Critical', value: props.vulnCountBySeverity.critical });
  if (props.vulnCountBySeverity.high > 0) data.push({ id: 'high', name: 'High', value: props.vulnCountBySeverity.high });
  if (props.vulnCountBySeverity.medium > 0) data.push({ id: 'medium', name: 'Medium', value: props.vulnCountBySeverity.medium });
  if (props.vulnCountBySeverity.low > 0) data.push({ id: 'low', name: 'Low', value: props.vulnCountBySeverity.low });
  if (props.vulnCountBySeverity.informational > 0) data.push({ id: 'informational', name: 'Informational', value: props.vulnCountBySeverity.informational });
  if (props.vulnCountBySeverity.unclassified > 0) data.push({ id: 'unclassified', name: 'Unclassified', value: props.vulnCountBySeverity.unclassified });

  const COLORS = {
    critical: colors.warmPurple,
    high: colors.redPink,
    medium: colors.tangerine,
    low: colors.apple,
    info: colors.grey2,
    informational: colors.grey2,
    default: colors.grey4
  };

  useEffect(() => {
    dispatch(getVulnsCountBySeverity());
  }, [dispatch, props.workspaceSelected]);

  const onPieEnter = (_data, index) => {
    setActiveIndex(index);
  };

  const onClick = (e) => {
    dispatch(setFilter('vulns', DASHBOARD_FILTERS.vulnerabilitiesBySeverity(e.id)));
    dispatch(redirect(`/manage/${props.workspaceSelected}`));
  };

  return (
    <Wrapper style={ { width: '100', height: 250 } }>
      <Row>
        <Col><Title>Vulnerabilities by severity</Title></Col>
      </Row>
      { data.length > 0
        ? (
        <ResponsiveContainer width="100%">
          <PieChart>
            <Pie
              activeIndex={ activeIndex }
              activeShape={ renderActiveShape }
              data={ data }
              cx="50%"
              cy="50%"
              innerRadius={ 55 }
              outerRadius={ 75 }
              fill="#8884d8"
              dataKey="value"
              onMouseEnter={ onPieEnter }
              onClick={ onClick }
            >
              {
                data.map((entry, index) => <Cell key={ `cell-${index}` } fill={ COLORS[entry.id] || COLORS.default } />)
              }
            </Pie>
          </PieChart>
        </ResponsiveContainer>
          )
        : <Empty>No vulnerabilities has been found yet.</Empty> }
    </Wrapper>
  );
};

export default SeverityChart;
