/* eslint-disable react/destructuring-assignment */
import React, { Component } from 'react';
import { Col, Row } from 'Common/Components/Grid';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import {
  getVulnsCount
} from 'Screens/Dashboard/actions/Actions';
import { DASHBOARD_FILTERS } from 'store/Filters/constants';
import { setFilter } from 'store/Filters/actions';
import { selectCurrentWorkspace } from 'store/Faraday/selectors';
import PropTypes from 'prop-types';
import { redirect } from 'store/Router/actions';
import {
  WrapperSeverity, StyledRow, SeverityCritical, SeverityHigh, SeverityMedium, SeverityLow, SeverityInformational, SeverityUnclassified, Title, Wrapper, Empty
} from './styled';

class Vulnerabilities extends Component {
  static calculatePercent (data, workspaceSelected) {
    if (data && data.groups && data.groups) {
      if (data.groups[workspaceSelected]) {
        let total_count = data.total_count;
        let critical_count = data.groups[workspaceSelected].critical ? data.groups[workspaceSelected].critical : 0;
        let high_count = data.groups[workspaceSelected].high ? data.groups[workspaceSelected].high : 0;
        let medium_count = data.groups[workspaceSelected].medium ? data.groups[workspaceSelected].medium : 0;
        let low_count = data.groups[workspaceSelected].low ? data.groups[workspaceSelected].low : 0;
        let info_count = data.groups[workspaceSelected].informational ? data.groups[workspaceSelected].informational : 0;
        let unclassified_count = data.groups[workspaceSelected].unclassified ? data.groups[workspaceSelected].unclassified : 0;
        const min_vulns = Math.ceil((6 * total_count) / 100);

        if (critical_count === 0 || critical_count < min_vulns) {
          total_count += min_vulns - critical_count;
          critical_count = min_vulns;
        }
        if (high_count === 0 || high_count < min_vulns) {
          total_count += min_vulns - high_count;
          high_count = min_vulns;
        }
        if (medium_count === 0 || medium_count < min_vulns) {
          total_count += min_vulns - medium_count;
          medium_count = min_vulns;
        }
        if (low_count === 0 || low_count < min_vulns) {
          total_count += min_vulns - low_count;
          low_count = min_vulns;
        }
        if (info_count === 0 || info_count < min_vulns) {
          total_count += min_vulns - info_count;
          info_count = min_vulns;
        }
        if (unclassified_count === 0 || unclassified_count < min_vulns) {
          total_count += min_vulns - unclassified_count;
          unclassified_count = min_vulns;
        }
      }
    }
  }

  constructor (props) {
    super(props);
    this.state = {};
    this.rootManage = this.rootManage.bind(this);
  }

  componentDidMount () {
    this.props.getVulnsCount(this.props.extraWorkspaces);
  }

  componentDidUpdate () {
    const { data, workspaceSelected } = this.props;
    this.constructor.calculatePercent(data, workspaceSelected);

    if (this.props.reload) this.props.getVulnsCount(this.props.extraWorkspaces);
  }

  rootManage (severity) {
    this.props.setFilter(severity);
    this.props.redirect(`/manage/${this.props.workspaceSelected}`);
  }

  render () {
    const { data, workspaceSelected } = this.props;
    let content = null;

    if (data && data.groups && data.groups) {
      const total_count = data.total_count;
      let critical_count = 0;
      let high_count = 0;
      let medium_count = 0;
      let low_count = 0;
      let info_count = 0;
      let unclassified_count = 0;

      if (data.groups[workspaceSelected]) {
        critical_count = data.groups[workspaceSelected].critical || 0;
        high_count = data.groups[workspaceSelected].high || 0;
        medium_count = data.groups[workspaceSelected].medium || 0;
        low_count = data.groups[workspaceSelected].low || 0;
        info_count = data.groups[workspaceSelected].informational || 0;
        unclassified_count = data.groups[workspaceSelected].unclassified || 0;
      }

      content = (
        <WrapperSeverity>
          <SeverityCritical title="Critical" onClick={ () => { this.rootManage('critical'); } } width={ critical_count / total_count }>{ critical_count }</SeverityCritical>
          <SeverityHigh title="High" onClick={ () => { this.rootManage('high'); } } width={ high_count / total_count }>{ high_count }</SeverityHigh>
          <SeverityMedium title="Medium" onClick={ () => { this.rootManage('medium'); } } width={ medium_count / total_count }>{ medium_count }</SeverityMedium>
          <SeverityLow title="Low" onClick={ () => { this.rootManage('low'); } } width={ low_count / total_count }>{ low_count }</SeverityLow>
          <SeverityInformational title="Info" onClick={ () => { this.rootManage('informational'); } } width={ info_count / total_count }>{ info_count }</SeverityInformational>
          <SeverityUnclassified title="Unclassified" onClick={ () => { this.rootManage('unclassified'); } } width={ unclassified_count / total_count }>{ unclassified_count }</SeverityUnclassified>
        </WrapperSeverity>
      );
    }

    return (
      <Wrapper>
        <Row>
          <Row>
            <Col><Title>Vulnerabilities by Severities</Title></Col>
          </Row>
        </Row>
        <Col>
          <StyledRow>
            { (data && data.groups && data.groups) ? content : <Empty>No vulnerabilities has been found yet.</Empty> }
          </StyledRow>
        </Col>
      </Wrapper>
    );
  }
}

const mapStateToProps = (state) => ({
  data: state.dashboard.tools[0].data,
  extraWorkspaces: state.dashboard.extraWorkspaces,
  error: state.dashboard.tools[0].error,
  reload: state.dashboard.reloadVulnerabilities,
  workspaceSelected: selectCurrentWorkspace(state)
});

const mapDispatchToProps = (dispatch) => ({
  getVulnsCount: (extraWorkspaces) => {
    dispatch(getVulnsCount(extraWorkspaces));
  },
  setFilter: (severity) => {
    dispatch(setFilter('vulns', DASHBOARD_FILTERS.vulnerabilitiesBySeverity(severity)));
  },
  redirect: (pathname) => {
    dispatch(redirect(pathname));
  }
});

Vulnerabilities.propTypes = {
  getVulnsCount: PropTypes.func.isRequired,
  reload: PropTypes.bool.isRequired
};
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Vulnerabilities));
