import React from 'react';
import { PropTypes } from 'prop-types';
import { FormattedMessage, useIntl } from 'react-intl';
import get from 'lodash/get';
import { ReactComponent as TriggerButton } from 'Images/icon-action-bar-trigger.svg';
import IconButton from 'Common/Components/IconButton';
import CheckBox from 'Common/Components/Checkbox/styled';
import DateFormatter from 'Common/Components/DateFormatter';
import Skeleton from 'Common/Components/Skeleton';
import { WrapperColumn, WrapperStatus } from './styled';

const Status = ({ value }) => {
  const intl = useIntl();
  return <WrapperStatus value={ value }>{intl.formatMessage({ id: `agents.table.columns.status.${value}` })}</WrapperStatus>;
};

const getColumns = (fields, onTriggerAgent, isFetching) => [{
  Header: () => <FormattedMessage id="agents.table.columns.id" />,
  id: 'id',
  accessor: 'id',
  resizable: false,
  show: false,
  Cell: ({ original }) => (isFetching ? <Skeleton /> : <WrapperColumn>{ original.id }</WrapperColumn>),
  custom_order: get(fields, 'id.order', 1)
}, {
  Header: () => <FormattedMessage id="agents.table.columns.name" />,
  id: 'name',
  accessor: 'name',
  resizable: false,
  Cell: ({ original }) => (isFetching ? <Skeleton /> : <WrapperColumn>{ original.name }</WrapperColumn>),
  custom_order: get(fields, 'name.order', 1),
  maxWidth: 350,
  minWidth: 250
}, {
  Header: () => <FormattedMessage id="agents.table.columns.trigger" />,
  id: 'trigger',
  resizable: false,
  Cell: ({ original }) => (isFetching ? <Skeleton /> : <WrapperColumn>{ original.is_online && original.active ? <IconButton onClick={ (e) => onTriggerAgent(e, original.id) } title="Trigger" icon={ <TriggerButton /> } /> : null }</WrapperColumn>),
  custom_order: get(fields, 'trigger.order', 1),
  maxWidth: 90,
  minWidth: 90
}, {
  Header: () => <FormattedMessage id="agents.table.columns.lastExecuted" />,
  id: 'last_run',
  accessor: 'last_run',
  resizable: false,
  Cell: ({ original }) => {
    if (isFetching) return <Skeleton />;
    if (original.last_run) {
      return (
        <WrapperColumn>
          <DateFormatter date={ original.last_run } />
        </WrapperColumn>
      );
    }
    return null;
  },
  custom_order: get(fields, 'lastExecuted.order', 1),
  maxWidth: 150,
  minWidth: 150
}, {
  Header: () => <FormattedMessage id="agents.table.columns.status" />,
  id: 'status',
  resizable: false,
  Cell: ({ original }) => (isFetching ? <Skeleton /> : <WrapperColumn><Status value={ original.status } /></WrapperColumn>),
  custom_order: get(fields, 'status.order', 1)
}, {
  Header: () => <FormattedMessage id="agents.table.columns.active" />,
  id: 'active',
  resizable: false,
  Cell: ({ original }) => (isFetching ? <Skeleton /> : <WrapperColumn><CheckBox checked={ original.active } readOnly /></WrapperColumn>),
  custom_order: get(fields, 'status.order', 1)
}, {
  Header: () => <FormattedMessage id="agents.table.columns.createdAt" />,
  id: 'create_date',
  accessor: 'create_date',
  resizable: false,
  Cell: ({ original }) => {
    if (isFetching) return <Skeleton />;
    if (original.create_date) {
      return (
        <WrapperColumn>
          <DateFormatter date={ original.create_date } />
        </WrapperColumn>
      );
    }
    return null;
  },
  custom_order: get(fields, 'createdAt.order', 1),
  maxWidth: 150,
  minWidth: 150
}];

getColumns.propTypes = {
  fields: PropTypes.instanceOf(Object).isRequired
};

export default getColumns;
