import React, { useEffect } from 'react';
import { Col, Row } from 'Common/Components/Grid';
import { useDispatch, useSelector } from 'react-redux';
import {
  getSummary
} from 'Screens/Dashboard/actions/Actions';
import { selectCurrentWorkspace } from 'store/Faraday/selectors';
import get from 'lodash/get';
import {
  StyledRow, Name, Title, Value, Empty
} from './styled';

const Summary = () => {
  const dispatch = useDispatch();
  const workspaceSelected = useSelector(selectCurrentWorkspace);
  const data = useSelector((state) => get(state, 'dashboard.tools[3].data', []));

  useEffect(() => {
    dispatch(getSummary());
  }, [dispatch, workspaceSelected]);

  return (
    <Row>
      <Col>
        <Row>
          <Col><Title>Workspace Summary</Title></Col>
        </Row>
        { (data && data.stats)
          ? (
          <StyledRow>
            <Col>
              <StyledRow><Value>{data.stats.hosts}</Value></StyledRow>
              <StyledRow><Name>Hosts</Name></StyledRow>
            </Col>

            <Col>
              <StyledRow><Value>{data.stats.services}</Value></StyledRow>
              <StyledRow><Name>Services</Name></StyledRow>
            </Col>

            <Col title="std + web">
              <StyledRow>
                <Value>{data.stats.total_vulns}</Value>
              </StyledRow>
              <StyledRow><Name>Total Vulns</Name></StyledRow>
            </Col>

            <Col>
              <StyledRow>
                <Value>{data.stats.web_vulns}</Value>
              </StyledRow>
              <StyledRow><Name>Web Vulns</Name></StyledRow>
            </Col>

          </StyledRow>
            )
          : <Empty>There is no Vulnerabilities to show.</Empty> }
      </Col>
    </Row>
  );
};

export default Summary;
