import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import IconButton from 'Common/Components/IconButton';
import { selectShowDetail } from 'store/Manage/selectors';
import TagIcon from './styled';
import { openModal } from 'store/modals/actions';
import { MODAL_UPGRADE_LICENSE_ACTION_BAR } from 'store/modals/modals';
import FFTags from 'Images/ff_tags.png';

const TagButton = ({ setFF }) => {
  const dispatch = useDispatch();
  const showVulnDetail = useSelector(selectShowDetail);

  const onClick = () => {
    setFF(FFTags, 'upgradeLicense.tags.description');
    dispatch(openModal(MODAL_UPGRADE_LICENSE_ACTION_BAR));
  };

  if (showVulnDetail) return null;
  return <IconButton icon={ <TagIcon /> } title="Tags" onClick={ onClick } />;
};

export default TagButton;
