// severity
export const SeverityStatusBox = styled.div`
  border-radius: 4px;
  box-shadow: 2px 2px 5px 0 rgba(0, 0, 0, 0.06), inset 0 -2px 0 0 ${colors.purple1};
  height: 40px;
  max-width: 2px;
  width: 2px;
  ${(props) => props.critical && css`
    background-color: ${colors.purple2};
    border: solid 1px ${colors.purple2};
  `}

  ${(props) => props.high && css`
    background-color: ${colors.redPink};
    border: solid 1px ${colors.redPink};
  `}

  ${(props) => props.medium && css`
    background-color: ${colors.orange1};
    border: solid 1px ${colors.orange1};
  `}

  ${(props) => props.low && css`
    background-color: ${colors.green1};
    border: solid 1px ${colors.green1};
  `}

  ${(props) => props.informational && css`
    background-color: ${colors.grey2};
    border: solid 1px ${colors.grey2};
  `}

  ${(props) => props.unclasified && css`
    background-color: ${colors.grey3};
    border: solid 1px ${colors.grey3};
  `}
`;
SeverityStatusBox.displayName = 'SeverityStatusBox';

