import filter from 'lodash/filter';
import {
  AGENTS_FETCHING,
  AGENTS_FAIL,
  AGENTS_SUCCESS,
  AGENTS_TABLE_GET_DATA,
  AGENTS_TABLE_SELECT_ROW,
  AGENTS_GET_TOKEN,
  AGENTS_GET_TOOLS,
  AGENTS_CREATE_WIZARD_RESET,
  AGENTS_CREATE_WIZARD_REMOVE_TOOL,
  AGENTS_CREATE_WIZARD_ADD_TOOL,
  AGENTS_CREATE_WIZARD_SET_FIELDS_TOOL,
  AGENTS_GET_MANIFEST,
  RESET_NEW_AGENT,
  SET_NEW_AGENT
} from './constants';

export const initialState = {
  isFetching: false,
  error: false,
  errors: {},
  success: false,
  errorMessage: '',
  token: '',
  tokenDuration: 60,
  table: {
    list: {
      data: [],
      total: 0,
      sortedBy: ''
    },
    fields: {
      id: {
        order: 1, visible: false
      },
      name: {
        order: 2, visible: true
      },
      status: {
        order: 3, visible: true
      },
      last_executed: {
        order: 4, visible: true
      },
      createdAt: {
        order: 5, visible: true
      }
    },
    selected: {
      rowsSelected: [], // filas seleccionadas
      lastRowIndexSelected: -1, // ultimo indice de fila seleccionada
      lastIndexForRange: -1, // ultimo rango de indice de fila seleccionada
      lastRowSelected: {} // ultima fila seleccionada.
    }
  },
  triggerAgent: {
    tools: {}
  },
  createAgent: {
    stepChooseName: { name: '' },
    stepChooseServer: { server: '' },
    stepChooseWorkspaces: [],
    stepChooseTools: [],
    stepDone: { token: '' }
  },
  agentTypes: {},
  createNewAgent: {
    name: '',
    server: '',
    workspaces: [],
    defaultChecked: true
  }
};

function agents (state = initialState, action) {
  switch (action.type) {
    case AGENTS_TABLE_SELECT_ROW: {
      const {
        newRowsSelected, lastRowIndexSelected, indexForRange, rowSelected
      } = action.rowSelectedInfo;

      return {
        ...state,
        isFetching: false,
        error: false,
        success: false,
        table: {
          ...state.table,
          selected: {
            lastRowSelected: rowSelected,
            rowsSelected: newRowsSelected,
            lastRowIndexSelected: lastRowIndexSelected || -1,
            lastIndexForRange: indexForRange || -1
          }
        }
      };
    }

    case AGENTS_FETCHING: {
      return {
        ...state,
        isFetching: action.isFetching,
        error: false,
        success: false
      };
    }

    case AGENTS_TABLE_GET_DATA: {
      return {
        ...initialState,
        isFetching: false,
        error: false,
        table: {
          ...state.table,
          list: {
            data: action.data,
            total: action.data.length
          }
        }
      };
    }

    case AGENTS_FAIL: {
      return {
        ...state,
        isFetching: false,
        error: true,
        errors: action.errors,
        success: false,
        message: action.message
      };
    }
    case AGENTS_SUCCESS: {
      return {
        ...state,
        isFetching: false,
        error: false,
        success: true
      };
    }

    case AGENTS_GET_TOKEN: {
      return {
        ...state,
        isFetching: false,
        error: false,
        token: action.token,
        tokenExpiresIn: action.tokenExpiresIn,
        tokenDuration: action.tokenDuration
      };
    }

    case AGENTS_GET_TOOLS: {
      return {
        ...state,
        isFetching: false,
        error: false,
        triggerAgent: {
          ...state.triggerAgent,
          agentId: action.id,
          tools: action.tools
        }
      };
    }

    case AGENTS_CREATE_WIZARD_RESET: {
      return {
        ...state,
        isFetching: false,
        error: false,
        createAgent: initialState.createAgent
      };
    }

    case AGENTS_CREATE_WIZARD_ADD_TOOL: {
      return {
        ...state,
        isFetching: false,
        error: false,
        createAgent: {
          ...state.createAgent,
          stepChooseTools: state.createAgent.stepChooseTools.concat(action.tool)
        }
      };
    }

    case AGENTS_CREATE_WIZARD_REMOVE_TOOL: {
      return {
        ...state,
        isFetching: false,
        error: false,
        createAgent: {
          ...state.createAgent,
          stepChooseTools: filter(state.createAgent.stepChooseTools, (e) => (
            e.name !== action.tool
          ))
        }
      };
    }

    case AGENTS_CREATE_WIZARD_SET_FIELDS_TOOL: {
      // se reemplaza el objeto con newitem.
      const newItem = [
        { name: action.toolName, variables: action.fields }
      ];
      const stepChooseTools = state.createAgent.stepChooseTools.map((obj) => newItem.find((o) => o.name === obj.name) || obj); // busco el objeto y reemplazo.

      return {
        ...state,
        isFetching: false,
        error: false,
        createAgent: {
          ...state.createAgent,
          stepChooseTools
        }
      };
    }
    case AGENTS_GET_MANIFEST: {
      return {
        ...state,
        agentTypes: action.data
      };
    }

    case SET_NEW_AGENT: {
      return {
        ...state,
        createNewAgent: {
          ...action.data
        }
      };
    }

    case RESET_NEW_AGENT: {
      return {
        ...state,
        createNewAgent: initialState.createNewAgent,
        createAgent: initialState.createAgent
      };
    }

    default: {
      return state;
    }
  }
}

export default agents;
