import { Accept, Cancel } from 'Common/Components/Button/styled';
import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { selectToolsByCategory, selectToolsSelected, selectTools } from 'store/Agents/selectors';
import Card from 'Screens/Automation/components/Agents/components/CreateAgent/components/ChooseTools/components/Card';
import { addTool } from 'store/Agents/actions';
import head from 'lodash/head';
import filter from 'lodash/filter';
import { useIntl } from 'react-intl';
import {
  Wrapper, Title, Description, Content, ButtonsContainer, ToolContainer, ToolCategory, Tools, WrapperTitle, WrapperButton, WrapperAdded
} from './styled';
import WebSiteButton from '../WebsiteButton';
import Toolbar from '../Toolbar/styled';

const Add = ({ added, onAdded }) => {
  const intl = useIntl();
  return !added && (
  <WrapperButton onClick={ onAdded }>
    {intl.formatMessage({ id: 'agents.createAgent.wizard.chooseTools.addToAgent' })}
  </WrapperButton>
  );
};

const Added = ({ added }) => {
  const intl = useIntl();
  return added && (
  <WrapperAdded>
    {intl.formatMessage({ id: 'agents.createAgent.wizard.chooseTools.added' })}
  </WrapperAdded>
  );
};

const ToolCollection = ({ onClose }) => {
  const intl = useIntl();
  const dispatch = useDispatch();

  const props = useSelector((state) => ({
    toolsByCategory: selectToolsByCategory(),
    toolsSelected: selectToolsSelected(state),
    allTools: selectTools()
  }));
  const [tools, setTools] = useState(props.toolsSelected);

  const isAdded = (tool) => {
    const toolsSelected = tools.map((e) => e.name);
    return toolsSelected.indexOf(tool) > -1;
  };

  const onAdded = (e, tool) => {
    const newTool = filter(props.allTools, { name: tool });
    const newTools = [...tools];
    newTools.push(head(newTool));
    setTools(newTools);
    e.preventDefault();
  };

  const onSave = (e) => {
    tools.forEach((tool) => {
      dispatch(addTool(tool.name));
    });
    e.preventDefault();
    onClose();
  };

  return (
    <Wrapper>
      <WrapperTitle>
        <Title>{intl.formatMessage({ id: 'agents.createAgent.wizard.chooseTools.toolCollection.title' })}</Title>
        <ButtonsContainer>
          <Cancel onClick={ onClose }>{intl.formatMessage({ id: 'agents.createAgent.wizard.chooseTools.toolCollection.back' })}</Cancel>
          <Accept onClick={ onSave }>{intl.formatMessage({ id: 'agents.createAgent.wizard.chooseTools.toolCollection.done' })}</Accept>
        </ButtonsContainer>
      </WrapperTitle>
      <Description>{intl.formatMessage({ id: 'agents.createAgent.wizard.chooseTools.toolCollection.description' })}</Description>
      <Content>
        {
        props.toolsByCategory.map((tool) => (
          <ToolContainer key={ tool.categoryName }>
            <ToolCategory>{ tool.categoryName }</ToolCategory>
            <Tools>
              {
              tool.items.map((item) => {
                const toolbar = (
                  <Toolbar>
                    <Added added={ isAdded(item.name) } />
                    <Add added={ isAdded(item.name) } onAdded={ (e) => onAdded(e, item.name) } />
                    <WebSiteButton url={ item.website } />
                  </Toolbar>
                );
                return <Card key={ item.title } title={ item.title } description={ item.description } image={ item.image } toolbar={ toolbar } />;
              })
}
            </Tools>
          </ToolContainer>
        ))
      }
      </Content>
    </Wrapper>
  );
};
export default ToolCollection;
